import React, { Component } from 'react';
import Menu from "../menu";
// import axios from 'axios';
import {getAnswer} from '../services';
//axios.defaults.baseURL = "http://localhost:2000/api/"

export default class Contact extends Component {

  constructor(props) {
    super(props);

    this.state = {
      user: '',
      loading: true,
      answer: 'Одоогоор хариу алга',
      hariu: 'hhe',
      input : '',
    };
  }
  componentDidMount(){
    getAnswer(100).then(response=>{this.setState({user:response.data,}), alert('Амжилттай '+JSON.stringify(response))})
  }
  findArea = () => {
    getAnswer(this.state.input).then(response=>{
      this.setState({
        user:response.data,
      })
    })
  }
  findArea2 = () => {
    alert('hho');
    
  }
  inputChange = (evt) =>{
    this.setState({
      input:evt.target.value,
    })
  }
  render() {
    return (
      <div>
        <Menu />
        {"user: "+(this.state.user.wide)}
        <p>{"harui: "+this.state.hariu}<br/>{"input: "+this.state.input}</p>
        <fieldset>
          <label for="nameField">Тойргийн талбай олох</label>
          <input type="text" placeholder="Радиус" value={this.state.input} onChange={this.inputChange}  />
          <input type="button" class="button"  onClick={this.findArea} name="asdf" value="show"/>
          <input type="button" class="button" onClick={this.findArea2} name="func2" value="show2"/>
        </fieldset>
        <p>{"answer: "+this.state.answer}</p>
      </div> 
    );
  }
}


